* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Roboto", sans-serif;
}
.App {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.icon {
  color: orange;
}
::-webkit-scrollbar {
  width: 7px;
  background-color: wheat;
}

::-webkit-scrollbar-thumb {
  background: lightgrey;
  border-radius: 7px;
}
@import "./styles-components/Main";
@import "./styles-components/nav";
@import "./styles-components/Aside";
@import "./styles-components/ChatSection";
@import "./styles-components/Sign";
